<template>
  <div class="contact">
    <h2>{{ `${firstname} ${lastname}` }}</h2>
    <ul class="contact-props">
      <li>{{dob}}</li>
      <li>{{address}}</li>
      <li>{{phone}}</li>
      <li>{{email}}</li>
    </ul>
  </div>
</template>

<script>
import * as styles from './contact.css';
export default {
  name: 'contact',
  data() {
    return {
      firstname: 'Tim',
      lastname: 'Hooker',
      address: '860 Lockland',
      dob: '12-21-1978',
      phone: 'nunya',
      email: 'timhooker@me.com'
    }
  }
}
</script>


