<template>
  <div>
    <h2>Contacts</h2>
    <ul>
      <li v-for="contact in contacts">
        <p>{{contact.firstname}} {{contact.lastname}}</p>
        <p>{{contact.email}}</p>
      </li>
    </ul>
    <router-link to="/contact-form">Go to Contact Form</router-link>
  </div>
</template>

<script>
import { getContacts } from '../api/contacts';
import Contact from './Contact';
export default {
  name: 'home',
  data () {
    return { contacts: [] };
  },
  mounted() {
    getContacts().then(res => {
      this.contacts = res;
    })
  },
  components: { contact: Contact }
}
</script>


