function getContacts() {
  return fetch('http://localhost:5000/api/contacts')
    .then(data => data.json())
    .catch(console.log);
}

function postContact(contact) {
  return fetch('api/contacts', {
      method: 'POST',
      credentials: include,
      data: contact
    }).then(data => data.json)
      .catch(console.log)
}

export {getContacts, postContact}


